<template>
  <div class="container1">
    <Main title="区块链溯源" title2="区块链">
      <el-input slot="input" placeholder="Search ID..." @keydown.enter.native="getList" prefix-icon="el-icon-search" v-model="id"
        clearable>
      </el-input>
      <template slot="table">
        <div class="result-container">
          <!-- -->
          <el-timeline v-if="list.length > 0" reverse>
            <el-timeline-item type="success" v-for="(item, index) in list" :key="index"
              :timestamp="item.timestamp.seconds" placement="top">
              <el-card>
                <p><span class="title">tx_id:</span> {{ item.tx_id }}</p>
                <p><span class="title">tx_value:</span>{{ item.value }}</p>
              </el-card>
            </el-timeline-item>
          </el-timeline>
          <el-empty v-else description="暂无数据"></el-empty>
        </div>
      </template>

    </Main>
  </div>
</template>
<script>
import { queryHistory } from "@/api";
import { Base64 } from 'js-base64';
import Main from "../common/Main.vue";
export default {
  components: {
    Main
  },
  data() {
    return {
      id: "",
      list: [],
    };
  },
  methods: {
    async getList() {
      const res = await queryHistory(this.id);
      this.list = res;
      if (this.list.length <= 0) return;
      this.list.forEach((item) => {
        item.timestamp.seconds = this.$dayjs
          .unix(item.timestamp.seconds)
          .format("YYYY-MM-DD HH:mm:ss");
        item.value = Base64.decode(item.value);
      });
    },
  },
  mounted() {
    // this.getList();
  },
};
</script>
